body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchBy {
    border-right: 1px solid #dee2e6;
}


.user-info{
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 3px rgba(0, 0, 0, 0.2);
    max-height: 470px;
}
.image{
    display: block;
    max-width: 100%;
    border-radius: 6px;
    width: 100%;
}

.repository-info{
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 3px rgba(0, 0, 0, 0.2);
}
.name{
    color: #4078c0;
}
.full-name{
    font-size: 12px;
}
.dot{
    height: 12px;
    width: 12px;
    background-color: #E34C31;
    border-radius: 50%;
    display: inline-block;
}
.repo-info{
    font-size: 11px;
}
.repo-lastUpdated{
    font-size: 13px;
}

.language-container{
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 3px rgba(0, 0, 0, 0.2);
}
.heading{
    font-size: 18px;
    font-weight: bold;
}
.language-list .language-item:nth-child(odd) {
    background: #ecebeb;
}
.user-image{
    /* display: block; */
    /* max-width: 100%; */
    width: 100%;
    /* width: 200px; */
    height: 200px;
}
.repo-details{
    min-width: 70%;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 3px rgba(0, 0, 0, 0.2);
}
